import { FC, useRef } from "react";
import { Editor } from "@tinymce/tinymce-react";

// TinyMCE so the global var exists
// sads eslint-disable-next-line no-unused-vars
// import tinymce from 'tinymce/tinymce';
import "../utils/TinyMce";
// Theme
import "tinymce/themes/silver";
// Toolbar icons
import "tinymce/icons/default";
import "tinymce/models/dom";

// Editor styles
import "tinymce/skins/ui/oxide/skin.min.css";
import "tinymce/skins/ui/oxide/content.min.css";

// importing the plugin js.
import "tinymce/plugins/advlist";
import "tinymce/plugins/autolink";
import "tinymce/plugins/link";
import "tinymce/plugins/image";
import "tinymce/plugins/lists";
import "tinymce/plugins/charmap";
import "tinymce/plugins/anchor";
import "tinymce/plugins/searchreplace";
import "tinymce/plugins/wordcount";
import "tinymce/plugins/code";
import "tinymce/plugins/fullscreen";
import "tinymce/plugins/insertdatetime";
import "tinymce/plugins/media";
import "tinymce/plugins/nonbreaking";
import "tinymce/plugins/table";
import "tinymce/plugins/template";
import "tinymce/plugins/help";

import "tinymce/plugins/autosave";
import "tinymce/plugins/emoticons";
import "tinymce/plugins/emoticons/js/emojiimages";
import "tinymce/plugins/emoticons/js/emojis";
import "tinymce/plugins/preview";
import "tinymce/plugins/quickbars";
import "tinymce/plugins/importcss";
import "tinymce/plugins/save";
import "tinymce/plugins/directionality";
import "tinymce/plugins/visualblocks";
import "tinymce/plugins/visualchars";
import "tinymce/plugins/codesample";
import "tinymce/plugins/pagebreak";

const MISEditor: FC<{ value: string; onChange: (value: string) => void }> = ({
	value,
	onChange,
}) => {
	const editorRef = useRef<any>(null);
	const init = (
		evt: any,
		editor: { editorContainer: { style: { fontFamily: string } } }
	) => {
		editorRef.current = editor;
		console.log(evt, editor);
		editor.editorContainer.style.fontFamily =
			"\"Vazirmatn RD UI FD\",Vazir,Vazirmatn,'Vazirmatn', EstedadVFFa, EVazir, Vazirmatn, sans-serif";
	};
	return (
		<Editor
			onInit={init}
			value={value}
			onEditorChange={onChange}
			init={{
				//@ts-ignore
				language: "fa",
				plugins:
					"print preview powerpaste casechange importcss tinydrive searchreplace autolink autosave save directionality advcode visualblocks visualchars fullscreen image link media mediaembed template codesample table charmap hr pagebreak nonbreaking anchor toc insertdatetime advlist lists checklist wordcount tinymcespellchecker a11ychecker imagetools textpattern noneditable help formatpainter permanentpen pageembed charmap tinycomments mentions quickbars linkchecker emoticons advtable export",
				menu: {
					tc: {
						title: "Comments",
						items: "addcomment showcomments deleteallconversations",
					},
				},
				menubar: "file edit view insert format tools table tc help",
				toolbar:
					"undo redo | bold italic underline strikethrough | fontfamily  fontselect fontsizeselect formatselect | alignleft aligncenter alignright alignjustify | outdent indent |  numlist bullist checklist | forecolor backcolor casechange permanentpen formatpainter removeformat | pagebreak | charmap emoticons | fullscreen  preview save print | insertfile image media pageembed template link anchor codesample | a11ycheck ltr rtl | showcomments addcomment",
				autosave_ask_before_unload: true,
				autosave_interval: "5s",
				autosave_prefix: "{path}{query}-{id}-",
				autosave_restore_when_empty: false,
				autosave_retention: "2m",
				image_advtab: true,
				link_list: [
					{ title: "My page 1", value: "https://www.tiny.cloud" },
					{ title: "My page 2", value: "http://www.moxiecode.com" },
				],
				image_list: [
					{ title: "My page 1", value: "https://www.tiny.cloud" },
					{ title: "My page 2", value: "http://www.moxiecode.com" },
				],
				image_class_list: [
					{ title: "None", value: "" },
					{ title: "Some class", value: "class-name" },
				],
				importcss_append: true,
				templates: [
					{
						title: "New Table",
						description: "creates a new table",
						content:
							'<div class="mceTmpl"><table width="98%%"  border="0" cellspacing="0" cellpadding="0"><tr><th scope="col"> </th><th scope="col"> </th></tr><tr><td> </td><td> </td></tr></table></div>',
					},
					{
						title: "Starting my story",
						description: "A cure for writers block",
						content: "Once upon a time...",
					},
					{
						title: "New list with dates",
						description: "New List with dates",
						content:
							'<div class="mceTmpl"><span class="cdate">cdate</span><br /><span class="mdate">mdate</span><h2>My List</h2><ul><li></li><li></li></ul></div>',
					},
				],
				template_cdate_format: "[Date Created (CDATE): %m/%d/%Y : %H:%M:%S]",
				template_mdate_format: "[Date Modified (MDATE): %m/%d/%Y : %H:%M:%S]",
				height: 320,
				image_caption: true,
				quickbars_selection_toolbar:
					"bold italic h1 h2 h3 blockquote quickimage quicktable",
				noneditable_noneditable_class: "mceNonEditable",
				toolbar_mode: "sliding",
				spellchecker_ignore_list: ["Ephox", "Moxiecode"],
				tinycomments_mode: "embedded",
				content_style:
					"@import url('https://clickmis.net/static/css/main.css?v=main.40d634b5.css'); body { font-family: \"Vazirmatn RD UI FD\",Vazir, Vazirmatn, sans-serif; } .mymention{ color: gray; }",
				contextmenu:
					"emoticons link quicklink quickimage image imagetools table",
				a11y_advanced_options: true,
				// skin: useDarkMode ? 'oxide-dark' : 'oxide',
				// content_css: useDarkMode ? 'dark' : 'default',
				font_family_formats:
					"Vazir=\"Vazirmatn RD UI FD\",Vazir,Vazirmatn,'Vazirmatn'; Andale Mono=andale mono,times; Arial=arial,helvetica,sans-serif; Arial Black=arial black,avant garde; Book Antiqua=book antiqua,palatino; Comic Sans MS=comic sans ms,sans-serif; Courier New=courier new,courier; Georgia=georgia,palatino; Helvetica=helvetica; Impact=impact,chicago;Symbol=symbol; Tahoma=tahoma,arial,helvetica,sans-serif; Terminal=terminal,monaco; Times New Roman=times new roman,times; Trebuchet MS=trebuchet ms,geneva; Verdana=verdana,geneva; Webdings=webdings; Wingdings=wingdings,zapf dingbats",
				promotion: false,
				save_oncancelcallback: function () {
					console.log("Save canceled");
				},
			}}
		/>
	);
};
export default MISEditor;
